body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
  overflow: hidden;
  position: fixed;
  width: 100%;
  top: 0;
  background-color: white;
  border-bottom: 2px solid black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 2em;
  font-weight: bold;
}

.navbar2{
  display: flex;
  align-items: center;
  margin: 2px;
}

.navbar_image{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 3px;
}

.navbar_button {
  padding: 5px 8px;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  color: white;
  background-color: black;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  border-radius: 5px;
}

.navbar_button:hover{
  transform: scale(1.05, 1.05);
}

/* .top{
  margin-top: 100px;
} */

.cards{
  width: 100%;
  height: auto;
}

.card{
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 0.1);
  background-color: #efefef;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card__img{
  width: 100%;
  height: 235px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card__info{
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card__category{
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
}

.card__title{
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  text-transform: capitalize;
}

.button1 {
  padding: 5px 8px;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  border-radius: 5px;
}

.button2{
  /* height: 20px; */
  width: 20px;
}

/* Display only on Laptop */
@media screen and (min-width: 1224px){
  .card:hover{
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.05, 1.05);
  }
}

/* Display only on Mobile */
@media screen and (max-width: 1224px){

  .top{
    margin-top: 80px;
  }
  /* .bottom{
    margin-bottom: 180px;
  } */
  
  .card{
    margin: 2%;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 0.1);
    background-color: #efefef;
    width: 46%;
    border-radius: 12px;
    box-shadow: 5px 5px 3px -1px rgba(0, 0, 0, 0.1);
    float: left;
  } 


  .card__img{
    width: 100%;
    height: 135px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }

  .card__info{
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    padding: 8px 12px 12px 12px;
  }

  .card__category{
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 12px;
    /* letter-spacing: 1px; */
    font-weight: 150;
    color: #868686;
  }

  .card__title{
    margin-top: 2px;
    margin-bottom: 5px;
    font-family: "Raleway", sans-serif;
    /* text-transform: capitalize; */
  }

  .button1 {
    padding: 5px 8px;
    font-family: "Raleway", sans-serif;
    font-size: 13px;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
    border-radius: 5px;
  }

  .navbar{
    overflow: hidden;
    position: fixed;
    width: 100%;
    top: 0;
    background-color: white;
    height: 70px;
    font-size: 1.17em;
  }

  .navbar_image{
    width: 65px;
    height: 65px;
  }

  .bottombar{
    overflow: hidden;
    background-color: white;
    position: fixed;
    bottom: 0;
    width: 100%;
  }
}
